/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
}
-webkit-text-size-adjust 100%,
-ms-text-size-adjust 100% {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #3f4040;
  font: normal 1.6rem/1.5 'Rubik';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #0098dc;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.header {
  flex-shrink: 0;
  text-align: center;
}
@media screen and (min-width: 480px) {
  .header__main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media screen and (min-width: 768px) {
  .header__main {
    display: block;
    position: relative;
    padding-top: 170px;
  }
}
@media screen and (min-width: 992px) {
  .header__main {
    height: 196px;
    padding-top: 0;
  }
}
.header__target {
  font-size: 1.8rem;
  text-transform: uppercase;
  border-bottom: 6px solid #dfdddb;
  padding: 15px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__target {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}
@media screen and (min-width: 768px) {
  .header__target {
    position: absolute;
    top: 20px;
    right: 0;
    left: 285px;
    width: auto;
    border-bottom: 0;
    padding: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__target {
    display: block;
    right: auto;
    left: 260px;
  }
}
@media screen and (min-width: 1200px) {
  .header__target {
    left: 300px;
  }
}
.header__target a {
  color: currentColor;
  text-decoration: underline;
}
.header__target a:hover {
  text-decoration: none;
}
.header__target a:last-child {
  display: block;
  margin-top: 10px;
}
@media screen and (min-width: 480px) {
  .header__target a:last-child {
    margin-top: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__target a:last-child {
    display: inline;
    margin-left: 30px;
  }
}
.header__logo {
  margin: 20px 0;
}
@media screen and (min-width: 480px) {
  .header__logo {
    width: 45%;
    text-align: left;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 768px) {
  .header__logo {
    position: absolute;
    top: 26px;
    left: 0;
    max-width: 260px;
    margin: 0;
  }
  .header__logo img {
    max-width: 200px;
  }
}
@media screen and (min-width: 992px) {
  .header__logo {
    max-width: 222px;
  }
  .header__logo img {
    max-width: 100%;
  }
}
.header__logo-slogan {
  font-size: 1.8rem;
  margin-top: 5px;
}
@media screen and (min-width: 992px) {
  .header__logo-slogan {
    width: 180px;
  }
}
.header__contacts {
  font-size: 1.8rem;
  border-bottom: 6px solid #dfdddb;
  padding-bottom: 15px;
}
@media screen and (min-width: 480px) {
  .header__contacts {
    width: 45%;
    border-bottom: 0;
    margin-top: 20px;
  }
}
@media screen and (min-width: 768px) {
  .header__contacts {
    position: absolute;
    top: 60px;
    right: 0;
    left: 285px;
    width: auto;
    text-align: left;
    padding-right: 155px;
    padding-bottom: 0;
    margin-top: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__contacts {
    top: 20px;
    right: 0;
    left: auto;
    width: 250px;
    text-align: center;
    padding-right: 0;
  }
}
@media screen and (min-width: 1200px) {
  .header__contacts {
    display: flex;
    flex-direction: column;
    top: 30px;
    width: 260px;
    line-height: 1;
    text-align: right;
  }
  .header__contacts-title {
    margin-bottom: 15px;
  }
}
.header__contacts-phone {
  color: #3f4040;
  font-weight: bold;
  font-size: 2rem;
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .header__contacts-phone {
    font-size: 2.4rem;
  }
}
@media screen and (min-width: 1200px) {
  .header__contacts-phone {
    margin: 10px 0;
  }
}
.header__contacts-workhours {
  font-weight: 300;
}
@media screen and (min-width: 1200px) {
  .header__contacts-workhours {
    margin: 10px 0;
  }
}
.header__contacts-links {
  font-weight: 500;
  margin-top: 5px;
}
@media screen and (min-width: 768px) {
  .header__contacts-links {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: 0;
    transform: translateY(-50%);
  }
}
@media screen and (min-width: 992px) {
  .header__contacts-links {
    position: static;
    margin-top: 15px;
  }
}
.header__contacts-links a {
  color: #38ae25;
  text-decoration: underline;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a {
    color: currentColor;
    margin-right: 10px;
  }
}
.header__contacts-links a:last-child {
  display: none;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a:last-child {
    display: inline;
    margin-right: 0;
  }
}
.header__contacts-links a:hover {
  text-decoration: none;
}
@media screen and (min-width: 992px) {
  .header__contacts-links a:hover {
    color: #38ae25;
  }
}
.header__action {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0 10px;
}
@media screen and (min-width: 480px) {
  .header__action {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    text-align: left;
    border-top: 6px solid #dfdddb;
    padding-top: 20px;
    margin-top: 0;
  }
}
@media screen and (min-width: 768px) {
  .header__action {
    text-align: center;
    border-top: 0;
    padding-top: 0;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 992px) {
  .header__action {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 67px;
    left: 260px;
    width: 440px;
  }
}
@media screen and (min-width: 1200px) {
  .header__action {
    top: 76px;
    left: 300px;
    width: 482px;
  }
}
.header__action-item {
  position: relative;
  color: #3f4040;
  font-size: 1.8rem;
  padding-top: 44px;
  padding-bottom: 5px;
  margin-bottom: 20px;
}
@media screen and (min-width: 480px) {
  .header__action-item {
    width: 30%;
  }
}
@media screen and (min-width: 768px) {
  .header__action-item {
    width: auto;
    white-space: nowrap;
  }
}
@media screen and (min-width: 992px) {
  .header__action-item {
    text-align: left;
    white-space: pre-wrap;
    margin-bottom: 0;
  }
  .header__action-item:first-child {
    width: 156px;
  }
  .header__action-item:nth-child(2) {
    width: 84px;
  }
  .header__action-item:last-child {
    width: 136px;
  }
}
.header__action-item::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #ed8223;
  content: '';
  transition: all 0.2s;
}
a:hover.header__action-item {
  text-decoration: none;
}
a:hover.header__action-item::after {
  width: 100%;
}
.header__action-item svg {
  position: absolute;
  top: 0;
  left: 50%;
  width: 35px;
  height: 35px;
  fill: #3f4040;
  transform: translateX(-50%);
  transition: all 0.2s;
}
@media screen and (min-width: 480px) {
  .header__action-item svg {
    left: 0;
    transform: none;
  }
}
@media screen and (min-width: 768px) {
  .header__action-item svg {
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (min-width: 992px) {
  .header__action-item svg {
    left: 0;
    transform: none;
  }
}
a:hover.header__action-item {
  color: #ed8223;
}
a:hover.header__action-item svg {
  fill: #ed8223;
}
.header__action-item:last-child {
  margin-bottom: 0;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
  background-color: #38ae25;
}
.nav.is_stuck {
  z-index: 10000;
}
.nav__cover {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  background-color: rgba(0,0,0,0.3);
}
.nav__menu {
  display: block;
  position: relative;
  color: #fff;
  font-size: 2.4rem;
  padding-left: 50px;
  margin: 10px 0;
}
@media screen and (min-width: 768px) {
  .nav__menu {
    display: none;
  }
}
.nav__menu::before,
.nav__menu::after {
  position: absolute;
  top: 50%;
  left: 0;
  width: 30px;
  content: '';
  transform: translateY(-50%);
}
.nav__menu::before {
  height: 20px;
  border: solid #fff;
  border-width: 2px 0;
}
.nav__menu::after {
  height: 2px;
  background-color: #fff;
}
.nav__main {
  overflow-y: auto;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  width: 260px;
  background-color: #fff;
  border: solid #0098dc;
  border-width: 10px 0;
  border-right: 2px solid #38ae25;
  padding: 10px 0;
  transform: translateX(-100%);
  transition: all 0.4s;
}
.nav__main--active {
  box-shadow: 0 0 25px rgba(0,0,0,0.8);
  transform: translateX(0);
}
@media screen and (min-width: 768px) {
  .nav__main--active {
    box-shadow: none;
    transform: none;
  }
}
@media screen and (min-width: 768px) {
  .nav__main {
    display: flex;
    flex-wrap: wrap;
    position: static;
    width: auto;
    background-color: transparent;
    border: 0;
    padding: 0;
    margin-bottom: 15px;
    margin-left: -25px;
    transform: none;
  }
}
@media screen and (min-width: 992px) {
  .nav__main {
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-left: 0;
  }
}
.nav__item {
  display: block;
  font-size: 1.8rem;
  text-transform: uppercase;
  border-bottom: 1px solid #0098dc;
  padding: 8px 15px;
}
@media screen and (min-width: 768px) {
  .nav__item {
    display: inline;
    color: #fff;
    border-bottom: 0;
    padding: 0;
    margin-top: 20px;
    margin-left: 25px;
  }
}
@media screen and (min-width: 992px) {
  .nav__item {
    margin-left: 0;
  }
}
@media screen and (min-width: 768px) {
  .nav__item--arrow {
    display: inline-flex;
    align-items: center;
  }
}
.nav__item--arrow::after {
  width: 13px;
  height: 13px;
  border: solid #fff;
  border-width: 0 0 2px 2px;
  margin-left: 10px;
  transform: translateY(calc(-50% + 1px)) rotate(-45deg);
  content: '';
}
/*  </nav>  */
/*  <footer>  */
