    max-width: none;
    padding: 0;
  }
}
@media screen and (min-width: 992px) {
  .homepage-general__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 47px 0 40px;
  }
}
@media screen and (min-width: 1200px) {
  .homepage-general__wrap {
    align-items: stretch;
  }
}
.homepage-general__about {
  color: #fff;
  text-align: center;
  background-image: linear-gradient(#dd9849 calc(100% - 94px), #ffbb6c calc(100% - 94px), #ffbb6c 100%);
  padding: 25px 8px 35px;
