/*  <buttons>  */
input,
button {
  outline: 0;
}
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 62px;
  color: #0098dc;
  font-size: 2.4rem;
  background-color: #fff;
  border: 0;
  border-radius: 31px;
  padding: 0 20px;
  box-shadow: 0 5px 0 #d8d7d7;
  cursor: pointer;
  outline: 0;
  transition: all 0.2s;
}
.btn:hover {
  text-decoration: none;
  background-color: #f2f2f2;
}
.btn:active {
  background-color: #f2f2f2;
  box-shadow: 0 5px 0 transparent;
  transform: translateY(3px);
}
.btn--orange {
  color: #fff;
  background-color: #ed8223;
  box-shadow: 0 5px 0 #bb661a;
}
.btn--orange:hover,
.btn--orange:active {
  background-color: #e47513;
}
.btn--gray {
  color: #3f4040;
  background-color: #cfcdcc;
  box-shadow: 0 5px 0 #b2b1b0;
}
.btn--gray:hover,
.btn--gray:active {
  color: #fff;
  background-color: #ed8223;
  box-shadow: 0 5px 0 #bb661a;
}
.btn--blue {
  color: #fff;
  background-color: #0098dc;
  box-shadow: 0 5px 0 #015276;
}
.btn--blue:hover,
.btn--blue:active {
  background-color: #0086c3;
}
.btn--small {
  height: 40px;
  font-size: 1.65rem;
  padding: 0 40px;
}
.btn--large {
  height: 78px;
  font-size: 3rem;
  border-radius: 39px;
  padding: 0 40px;
}
.btn--fullwidth {
  width: 100%;
}
/*  </buttons>  */
/*  <inputs>  */
.input-text,
.textarea {
  width: 100%;
  height: 56px;
  font-size: 1.8rem;
  background-color: #fff;
  border: 0;
  padding: 0 15px;
  outline: 0;
}
.input-text::placeholder,
.textarea::placeholder {
  opacity: 1;
  color: #292929;
}
.input-text--icon,
.textarea--icon {
  padding-left: 52px;
}
.textarea {
  height: 160px;
  min-height: 160px;
  padding: 10px 15px;
  resize: vertical;
  transition: box-shadow 0.2s;
}
.input-file {
  position: absolute;
  left: -9999px;
}
.input-file + label {
  display: inline-flex;
  align-items: center;
  height: 52px;
  color: #fff;
  font-weight: 300;
  font-size: 2.1rem;
  background-color: #ed8223;
  border-radius: 26px;
  padding: 0 30px;
  box-shadow: 0 5px 0 #bb661a;
  cursor: pointer;
  transition: all 0.2s;
  -webkit-user-select: none;
  user-select: none;
}
.input-file + label:hover {
  background-color: #e47513;
}
.input-file + label:active {
  box-shadow: 0 5px 0 transparent;
  transform: translateY(3px);
}
.input-file + label svg {
  width: 30px;
  height: 32px;
  fill: #fff;
  margin-right: 13px;
}
/*  </inputs>  */
/*  <layout>  */
.form__row {
  margin-bottom: 15px;
}
.form__row:first-child {
  margin-top: 0;
}
.form__row:last-child {
  margin-bottom: 0;
}
.form__row--icon {
  position: relative;
}
.form__row--icon .input-text {
  padding-left: 52px;
}
.form__row--icon .input-text:focus + svg {
  fill: #ed8223;
}
.form__row--icon svg {
  position: absolute;
  top: 50%;
  left: 11px;
  width: 30px;
  height: 30px;
  fill: #292929;
  transform: translateY(-50%);
  transition: all 0.2s;
}
.page-title {
  position: relative;
  font-weight: 500;
  font-size: 3.6rem;
  line-height: 4.8rem;
  text-transform: uppercase;
  text-align: center;
  padding-top: 40px;
}
@media screen and (min-width: 640px) {
  .page-title {
    text-align: left;
  }
}
.page-title::before {
  position: absolute;
  top: 0;
  left: 50%;
  width: 218px;
  height: 22px;
  content: '';
  transform: translateX(-50%);
}
@media screen and (min-width: 640px) {
  .page-title::before {
    left: 0;
    transform: none;
  }
}
.page-title--blue::before {
  background-color: #0098dc;
}
.page-title--green::before {
  background-color: #38ae25;
}
.page-title--orange::before {
  background-color: #ed8223;
}
.color-blue {
  color: #0098dc;
}
.color-green {
  color: #38ae25;
}
.color-orange {
  color: #ed8223;
}
.color-gray {
  color: #a3a3a2;
}
.no-scroll {
  overflow: hidden;
}
/*  </layout>  */
/*  <animation>  */
/*  </animation>  */
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
